import React, { useState } from 'react';
import { LoginUtil } from '../../services/loginUtil'
import { API } from '../../shared/constants/api.constants';
import Http from '../../http/http';

const ResetPassword = (props) => {
    const [userName, setUserName] = useState('');

    const goToLoginPage = () => {
        props.loginPage();
    }

    const submitResetDetails = () => {
        if(!userName.includes("@")) {
            setUserName(userName.replace(/([\/\,\!\\\^\$\{\}\[\]\(\)\\ \.\*\?\|\<\>\-\&])/g, "").replace("+", "00"));
        }
        // props.resetSubmit(userName);
        resetSubmitAction(userName)
        setUserName('');
    }

    const handleUserNameChange = (event: any) => {
        event.target.value = LoginUtil.formatPhone(event.target.value)
        setUserName(event.target.value);
    }

    function resetSubmitAction(viewAction: any) {
        let url = `${API.ResetUrl}`;
        Http.post(url, {
            baseUrl: '', data: {
                username: viewAction,
                client_id: process.env.REACT_APP_CLIENT_ID,
                client_secret: process.env.REACT_APP_CLIENT_SECRET,
                resetLink: window.location.protocol + "://" + window.location.host + '/changePassword',
                usedHost: window.location.protocol + "://" + window.location.host
            }
        }).then(sendTokenSuccess).catch((err) => {
            console.log(err);
        });
    }

    function sendTokenSuccess(data: any) {

        // let currentUser = LoginUtil.validateLogin(data.email);
        // let type = (currentUser === 'phone') ? ' SMS ' : ' an e-mail ';
        // setOpenAlert(true);
        // setAlertMessage('Need to change your password? No problem. We just sent you' + type
        // + 'with instructions. Follow the directions there to change your password.');
        window.location.pathname = '/login';
    }

    return (
        <div className='auth-container d-flex justify-content-center'>
            <div className='auth-main'>

                <div className='logo-container'>
                    <div className='logo-box'></div>
                </div>
                <div className='main-text mt-1'>
                    Forgot your password?
                </div>
                <div className="form-fields mt-5">
                    <div className='mb-4'>
                        <label htmlFor='email' className='form-label'>What is your email or phone number</label>
                        <input type='email' className='form-control' id='email' value={userName} onChange={handleUserNameChange} />
                    </div>
                </div>
                <div className='mt-4 mb-2 text-center'>
                    <button type="button" className="btn btn-primary auth-action-btn" onClick={submitResetDetails}>Reset My Password</button>
                </div>
                <div className='sub-text mb-4 mt-4'>
                    I remember my password. <a href="#" onClick={goToLoginPage}>Sign in now.</a>
                </div>
                <div className='small-text mb-2'>
                    ProofPilot takes your security and privacy seriously. We collect contact information to secure access to the sensitive information you share.
                </div>
            </div>
        </div>

    )
}

export default ResetPassword;