    /**
 * Get Country code
 * @return {*}
 */

    static getCountryCode() {
        let ipinfo = StorageService.getItem('ipinfo');
        if (ipinfo) {
            return this.COUNTRY_CODES[ipinfo.country]
        } else {
            return null;
        }
    }

    static isUserLoggedIn() {
        return this.me() && this.getToken();
    }

    static getToken() {
        return StorageService.getItem('access_token');
    }


    static me() {
        return StorageService.getItem('user_id');
    }

    static setToken(token) {
        StorageService.setItem('access_token', token);
    }

