import React, { useState } from 'react';
import { LoginUtil } from '../../services/loginUtil';
import Http from '../../http/http';
import { StorageService } from '../../services/storage.service';
import { UserService } from '../../services/userService';
// import { useNavigate } from "react-router-dom";
import { API } from '../../shared/constants/api.constants';
import { LoginProps } from '../../models';
// import { UserService } from '../../shared/userService';


function Login(props:LoginProps) {
    // const navigate = useNavigate();
    const [emailValid, setEmailValid] = useState(false);
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [passwordValid, setPasswordValid] = useState(false);
    const [emailBlurred, setEmailBlurred] = useState(false);
    const [passwordBlurred, setPasswordBlurred] = useState(false);
    const [apiError, setApiError] = useState('');

    const submitHandler = () => {
        setEmailBlurred(true);
        setPasswordBlurred(true);
        if (emailBlurred && emailValid && passwordBlurred && passwordValid) {
            // const loginObject = {
            //     email: email,
            //     password: password,
            //     keepmeLogIn: true
            // };
            // props.loginSubmit(loginObject);
            // props.loginSubmit("circulares");
            // User.logIn({
            //     username: User.login,
            //     password: vm.password,
            //     remember_me:  vm.keepmeLogIn
            // }).then(loginSuccess, loginError);
            //   setToken(token);
            let url = `${API.AuthUrl}`;
            Http.post(url, {
                apiBaseUrl: '/oauth/v2', 
                data: {
                    username: email,
                    password: password,
                    remember_me: true,
                    client_id: process.env.REACT_APP_CLIENT_ID,
                    client_secret: process.env.REACT_APP_CLIENT_SECRET,
                    grant_type: process.env.REACT_APP_GRANT_TYPE
                }
            }).then((data: any) => {
                UserService.setToken(data.data.access_token);
                // API.users().me().$promise.then(function (response) {
                //     //storeUserData(response, data.access_token);
                //     saveUserData(response, data.access_token);
                //     deferred.resolve(response);
                // });
                
                Http.get('/me', {
                    baseUrl: '',
                }, ).then((resp: any) => {
                    // UserService.setToken(data.data.access_token);
                    // UserService.saveUserData(resp.data.data, data.data.access_token);
                    alert('social login done');
                    // navigate({
                    //     pathname: '/'
                    // });
        
                }).catch(() => {
                    alert('social login error')
                    // navigate({
                    //     pathname: '/'
                    // });
        
                });
                // navigate({
                //     pathname: '/'
                // });
    
            }).catch((error) => {
                // vm.processing = false;
                if (error.response.status == -1) {
                    // vm.error = 'Error connecting to server.';
                    // setUserObj((prev) => {
                    //     const obj = { ...prev };
                    //     obj.error = "Error connecting to server.";
                    //     return obj;
                    // });
                    setApiError("Error connecting to server.");
                    return;
                };
    
                // flash.error = ''; // handled here, no need to show again
    
                // let message = errors.getRejectionMessage(error);
    
                if (error.response.data.code === 422) { // wrong password
                    // setUserObj((prev) => {
                    //     const obj = { ...prev };
                    //     obj.error = "Wrong password";
                    //     return obj;
                    // });
                    setApiError("Wrong password");
                } else if (error.response.data.code === 403) {
                    // Account locked
                    // setUserObj((prev) => {
                    //     const obj = { ...prev };
                    //     obj.error = "At ProofPIlot we take security very seriously. " +
                    //         "For various reasons, your account has been locked. " +
                    //         "If you did not receive an e-mail or SMS to reset your account," +
                    //         '<a href="/reset">please click here to manually initiate a reset.</a>';
                    //     return obj;
                    // });
                    setApiError("At ProofPIlot we take security very seriously. " +
                    "For various reasons, your account has been locked. " +
                    "If you did not receive an e-mail or SMS to reset your account," +
                    '<a href="/reset">please click here to manually initiate a reset.</a>');
    
                    //   vm.error = $sce.trustAsHtml(vm.error);
                } else {
                    // vm.error = message;
                };
            });
        }

    }

    const goToSignUp = () => {
        if(props.signUp) {
            props.signUp();
        }
       

    }
    const goToResetPage = () => {

        if(props.resetPage) {
            props.resetPage();
        }

    }


    const handleEmailChange = (event: any) => {
        let value = LoginUtil.validateLogin(event.target.value);
        if (value) {
            setEmailValid(true);
        } else {
            setEmailValid(false);
        }
        if (value === "phone") {
            // $rootScope.loginViaPhone = value;
            event.target.value = LoginUtil.formatPhone(event.target.value);
        }
        setEmail(event.target.value);
    }

    const handlePassWordChange = (event: any) => {
        if (event.target.value) {
            setPasswordValid(true);
        } else {
            setPasswordValid(false);
        }
        setPassword(event.target.value);
    }

    const emailBlurChange = () => {
        setEmailBlurred(true);
    }
    const passwordBlurChange = () => {
        setPasswordBlurred(true);
    }

    const googleLoginFunction = () => {
        // e.preventDefault();
        var loginWindow;
        let marginLeft = (screen.availWidth - 800)/2;
        let marginTop = (screen.availHeight - 600)/2;
        let requestAttrs = {
            usedHost: 'https://go-stagingnew.proofpilot.com',
            // usedHost: 'https://localhost:9000/login',
            client_id: '1_4bme99a1m2o00gowg04w00ccscg0gg0sksossg8w4cg4gw4wgw',
            client_secret: '1d3y4o0qiv8ks4s4ogkswk44cks4kk8k0o4sogoco4w4k0g4sk'
        };

        // dirty hack to send post request to modal window
        var form = document.createElement('form');
        form.setAttribute('method', 'POST');
        form.setAttribute('action', 'https://api-test.proofpilot.com/social/google');
        form.setAttribute('target', 'formresult');
        form.setAttribute('style', 'display: none;'); 
        for(let key in requestAttrs) {
            var hiddenField = document.createElement("input");
            hiddenField.setAttribute("name", key);
            hiddenField.setAttribute("value", requestAttrs[key]);
            form.appendChild(hiddenField);
        }
        document.body.appendChild(form);

        loginWindow = window.open('', 'formresult', `top=${marginTop}, left=${marginLeft}, width=800, height=600`);
        form.submit();
        document.body.removeChild(form);                

        function processMessage(event) {
            let parsedData = JSON.parse(event.data);
            if (loginWindow && parsedData.access_token) {
                loginWindow.close();
                window['socialAuthResponse'] = parsedData;
                // let callbackURL = window.location.searchParams.get('callbackURL');
                StorageService.setItem('fedral',true);
                // UserService.setUser(window['socialAuthResponse'], () => {});
                if(props.socialLoginSubmit) {
                    props.socialLoginSubmit(window['socialAuthResponse']);
                }
               
                
            } else if (loginWindow) {
                loginWindow.close();
            }
        }

        window.addEventListener('message', processMessage);



        return false;
    }

    // useEffect(() => {
    //     if (props.userObj) {
    //         setUserObj(props.userObj);
    //     }
    // }, [props.userObj])

    return (
        <div className='auth-container login-box d-flex justify-content-center'>
            <div className='auth-main'>

                <div className='logo-container'>
                    <div className='logo-box'></div>
                </div>
                <div className='main-text mt-1'>
                    Log In sekhar sadgsadg
                </div>
                <div className='sub-text'>
                    Continue with one of the following
                </div>
                <div className='social-login-btn-container'>
                    <div className='row'>
                        <div className='col'>
                            <div className='d-flex justify-content-center'>
                                <button className='social-login-btn' onClick={googleLoginFunction}>
                                            <div className='social-btn-wrapper'>
                                                <div className='social-btn-logo google'></div>
                                                <div className='social-btn-spacer'></div>
                                                <div className='social-btn-text'>Google</div>
                                            </div>
                                </button>
                            </div>
                        </div>
                        <div className='col'>
                            <div className='d-flex justify-content-center'>
                                <button className='social-login-btn'>
                                    <div className='social-btn-wrapper'>
                                        <div className='social-btn-logo google'></div>
                                        <div className='social-btn-spacer'></div>
                                        <div className='social-btn-text'>Microsoft</div>
                                    </div>
                                </button>
                            </div>
                        </div>
                        <div className='col'>
                            <div className='d-flex justify-content-center'>
                                <button className='social-login-btn'>
                                    <div className='social-btn-wrapper'>
                                        <div className='social-btn-logo google'></div>
                                        <div className='social-btn-spacer'></div>
                                        <div className='social-btn-text'>Apple</div>
                                    </div>
                                </button>
                            </div>
                        </div>
                        <div className='col'>
                            <div className='d-flex justify-content-center'>
                                <button className='social-login-btn'>
                                    <div className='social-btn-wrapper'>
                                        <div className='social-btn-logo google'></div>
                                        <div className='social-btn-spacer'></div>
                                        <div className='social-btn-text'>Single Sign-On</div>
                                    </div>
                                </button>
                            </div>

                        </div>
                    </div>
                </div>
                <div className='sub-text'>
                    It's faster, easier, and more secure.
                </div>

                <div className='circle-with-line-in-center d-flex justify-content-center'>
                    <div className='main-circle'>or</div>
                </div>
                <div className="form-fields">
                    <div className='mb-4'>
                        <label htmlFor='email' className='form-label'>E-mail or phone number</label>
                        <input type='email' className={
                            "form-control " +
                            ((emailBlurred && !emailValid) ? "field-error-border" : "")
                        } id='email'
                            onChange={handleEmailChange} value={email} onBlur={emailBlurChange} />
                        {(emailBlurred && !emailValid) && <p className='field-error'>Login is not correct</p>}
                    </div>
                    <div className='mb-3'>
                        <label htmlFor='password' className='form-label d-flex justify-content-between'>
                            <div>Password</div>
                            <div>
                                <a href="#" onClick={goToResetPage}>I've forgotten my password</a>
                            </div>
                        </label>
                        <input type='password' className={
                            "form-control " +
                            ((passwordBlurred && !passwordValid) ? "field-error-border" : "")
                        } id='password' onChange={handlePassWordChange} onBlur={passwordBlurChange} />
                        {(passwordBlurred && !passwordValid) && <p className='field-error'>Enter Password</p>}
                        {(apiError) && <p className='field-error' dangerouslySetInnerHTML={{ __html: apiError }}></p>}
                    </div>
                    <div className='form-check'>
                        <input className='form-check-input' type='checkbox' value='' id='flexCheckDefault' />
                        <label className='form-check-label' htmlFor='flexCheckDefault'>
                            Keep me logged in for 30 days
                        </label>
                    </div>
                </div>
                
                <div className='small-text'>
                    Use this feature in combination with your device security features to make it easier to log in and use ProofPilot. If you share your device with others or do not use device security features, do not use this feature.
                </div>
                <div className='mt-4 mb-2 text-center'>
                    <button type="button" className="btn btn-primary auth-action-btn" onClick={submitHandler}>Sign In</button>
                </div>
                <div className='sub-text mb-4'>
                    Don't have an account? <a href="#" onClick={goToSignUp}>Create one here.</a>
                </div>
                <div className='small-text mb-2'>
                    ProofPilot takes your security and privacy seriously. We collect contact information to secure access to the sensitive information you share.
                </div>
            </div>
        </div>
    )
}

export default Login;