import axios, { AxiosStatic } from "axios";
import { HttpPayload } from "./http.models";

 class HttpClient{
    static apiBaseUrl = '';
    static setAPIBaseURL(opt: {baseURL: string; apiBaseURL: string}) {
        this.apiBaseUrl = opt.apiBaseURL;
        axios.defaults.baseURL = opt.baseURL;
    }
    static getAxios():AxiosStatic{
        return axios;
    }
    static get(url: string, payload?: HttpPayload) {
       return axios(this.payload('GET', url, payload));
    }

    static post(url: string, payload?: HttpPayload) {
        return axios(this.payload('POST', url, payload));
    }

    static put(url: string, payload?: HttpPayload) {
        return axios(this.payload('PUT', url, payload));
    }

    static patch(url: string, payload?: HttpPayload) {
        return axios(this.payload('PATCH', url, payload));
    }

    static delete(url: string, payload?: HttpPayload) {
        return axios(this.payload('DELETE', url, payload));
    }

    static payload(method: string, url: string, payloadObj?: HttpPayload){
        const mapPayload = payloadObj || {};
        const obj = {...mapPayload};

        const URI = this.buildUrl(url, payloadObj || {});
        
        const payload = {
            url: URI,
            method,
            ...obj
        };
        return payload
    }

    static buildUrl(url: string, payload: HttpPayload) {
        let URI = (payload.baseURL ||  HttpClient.apiBaseUrl) + url;
        let params = URI;
        const payloadObj = payload || {};
        if(payloadObj.paramsData) {
            payload.paramsData?.forEach((ele: any[]) => {
                params= this.mapParams(params, ele[0], ele[1]);
            });
        }

        if(payloadObj.queryParams) {
            params += this.buildQuery(payload.queryParams);
        }

        if(payloadObj.queryParamsArray) {
            params += this.buildQuery(payload.queryParamsArray);
        }
        return params;
    }

    static mapParams(url: string, str: string, val: string): string {
        return url.replace(str, val);
    }

    private static buildQuery(obj: any): string{
        if(!obj) {
            return '';
        }
        
        let str = '';
        let count = 0;

        if(Array.isArray(obj)) {
            obj.forEach((ele, ind) => {
                if(ind === 0){
                    str += '?'+ ele[0] +'='+ ele[1];
                }else {
                    str += '&'+ele[0] +'='+ ele[1];
                }
            });
            return str;
        }

        for(let key in obj){
           if(count === 0){
            str += '?'+ key +'='+ obj[key];
           }else {
            str += '&'+key +'='+ obj[key];
           }
           count++;
        }
        return str;
    }
    
}

export default HttpClient;