import React, {useRef, useState } from 'react';
import { LoginUtil } from '../../services/loginUtil';
import { API } from '../../shared/constants/api.constants';
import Http from '../../http/http';
import { UserService } from '../../services/userService';
// import { useNavigate } from "react-router-dom";

const SignUp = (props) => {
    // const navigate = useNavigate();
    const [isFirstScreen, setIsFirstScreen] = useState(true);
    // const [useObj, setUseObj] = useState(null);
    const [emailValid, setEmailValid] = useState(false);
    const [email, setEmail] = useState('');
    const [emailBlurred, setEmailBlurred] = useState(false);
    const [passwordError, setPasswordError] = useState('');
    const inputPassRef = useRef<HTMLInputElement>(null);
    const inputRePassRef = useRef<HTMLInputElement>(null);
    const [apiError, setApiError] = useState('');
    const goToLoginPage = () => {
        props.loginPage();
    }
    const emailSubmitHandler = () => {
        setEmailBlurred(true);
        if (emailBlurred && emailValid) {
            const loginObject = {
                email: email,
                password: "dummy password just to check if user exist"
            };
            // props.signupEmailSubmit(loginObject);
            signupEmailSubmitAction(loginObject);
        }
    }

    const passwordSubmitHandler = () => {
        handlePassWordChange();
        if (passwordError) {
            return;
          }

          const loginpasswordObject = {
            email: email,
            password: inputPassRef.current?.value,
            keepmeLogIn: true
        };
        //   props.signupPasswordSubmit(loginpasswordObject);
        signupPasswordSubmitAction(loginpasswordObject)
    }

    const handleEmailChange = (event: any) => {
        let value = LoginUtil.validateLogin(event.target.value);
        if (value) {
            setEmailValid(true);
        } else {
            setEmailValid(false);
        }
        if (value === "phone") {
            // $rootScope.loginViaPhone = value;
            event.target.value = LoginUtil.formatPhone(event.target.value);
        }
        setEmail(event.target.value);
    }

    
    const emailBlurChange = (event: any) => {
        setEmailBlurred(true);
    }

    const handlePassWordChange = () => {
        var pattern = new RegExp(/^[a-zA-Z0-9!@#\$%\^\&*\)\(+=._-]{8,}$/);
    
        if (!pattern.test((inputPassRef.current?.value || '')) || !pattern.test((inputRePassRef.current?.value || ''))) {
            setPasswordError("Minimum 8 characters with 1 letter & 1 number"); 
          return;
        }
        if (inputPassRef.current?.value !== inputRePassRef.current?.value) {
            setPasswordError("Passwords should match");
          return;
        }
        setPasswordError('');
    }

    function signupEmailSubmitAction(viewToShow: any) {
        let url = `${API.AuthUrl}`;
        Http.post(url, {
            baseUrl: '/oauth/v2', data: {
                username: viewToShow.email,
                password: viewToShow.password,
                client_id: process.env.REACT_APP_CLIENT_ID,
                client_secret: process.env.REACT_APP_CLIENT_SECRET,
                grant_type: process.env.REACT_APP_GRANT_TYPE
            }
        }).then((data: any) => {
            // UserService.setToken(data.data.access_token);
            // API.users().me().$promise.then(function (response) {
            //     //storeUserData(response, data.access_token);
            //     saveUserData(response, data.access_token);
            //     deferred.resolve(response);
            // });
            // navigate({
            //     pathname: '/'
            // });

        }).catch((error) => {
            // console.log("my error is", error);
            // vm.processing = false;
            // if (error.status === -1) {
            //   vm.error = "Error connecting to server.";
            //   return;
            // }

            // flash.error = ""; // handled here, no need to show again

            // let message = errors.getRejectionMessage(error);


            // if (window.location.search.token && vm.validToken == true) {
            //   if (vm.checkTokenUser === false) {
            //     $location.path("/create_password");
            //     User.login = vm.login;
            //   } else {
            //     User.login = vm.login;
            //     $location.path("/password");
            //   }
            // } else 
            if (
                error.response.data.code === 422 &&
                error.response.data.description === "Invalid password!"
            ) {
                // wrong password

                //   $location.path("/password");
                //   User.login = vm.login;
                // setUserObj((prev) => {
                //     const obj = { ...prev };
                //     obj.error = "Your Account already exists. Please login to your account";
                //     return obj;
                // });
                setApiError("Your Account already exists. Please login to your account");
            } else if (error.response.data.code === 404) {
                // no such user
                // setUserObj((prev) => {
                //     const obj = { ...prev };
                //     obj.isFirstScreen = false;
                //     return obj;
                // });
                setIsFirstScreen(false);
            } else if (error.response.data.code === 403) {
                // Account locked
                // setUserObj((prev) => {
                //     const obj = { ...prev };
                //     obj.error = "At ProofPIlot we take security very seriously. " +
                //         "For various reasons, your account has been locked. " +
                //         "If you did not receive an e-mail or SMS to reset your account," +
                //         '<a href="/reset">please click here to manually initiate a reset.</a>';
                //     return obj;
                // });
                setApiError("At ProofPIlot we take security very seriously. " +
                "For various reasons, your account has been locked. " +
                "If you did not receive an e-mail or SMS to reset your account," +
                '<a href="/reset">please click here to manually initiate a reset.</a>');


                //   vm.error = $sce.trustAsHtml(vm.error);
            } else {
                // userObj.error = message;
            }
        });
        // User.logIn().then(loginSuccess, loginError);

        // navigate({
        //     pathname: '/'
        // });
    }

    function signupPasswordSubmitAction(object: any) {
        let url = `${API.Register}`;
        Http.post(url, {
            baseUrl: '', data: {
                registration: {
                    username: object.email,
                    fullname: object.email,
                    plainPassword: object.password,
                    remember_me: object.keepmeLogIn
                },
                client_id: process.env.REACT_APP_CLIENT_ID,
                client_secret: process.env.REACT_APP_CLIENT_SECRET,
                usedHost: window.location.protocol + "://" + window.location.hostname
            }
        }).then((data: any) => {
            let url = `${API.AuthUrl}`;
            Http.post(url, {
                baseUrl: '/oauth/v2', data: {
                    username: object.email,
                    password: object.password,
                    remember_me: object.keepmeLogIn,
                    client_id: process.env.REACT_APP_CLIENT_ID,
                    client_secret: process.env.REACT_APP_CLIENT_SECRET,
                    grant_type: process.env.REACT_APP_GRANT_TYPE
                }
            }).then((data: any) => {
                UserService.setToken(data.data.access_token);
                // API.users().me().$promise.then(function (response) {
                //     //storeUserData(response, data.access_token);
                //     saveUserData(response, data.access_token);
                //     deferred.resolve(response);
                // });
                // navigate({
                //     pathname: '/'
                // });

            }).catch((err) => {
                alert('raj fail')
            });

        }).catch((err) => {
            alert('raj fail')
        });
    }
    
    // useEffect(() => {
    //  if(props.userObj) {
    //     // setUseObj(props.userObj);
    //     setIsFirstScreen(props.userObj.isFirstScreen)
    //  }
    // }, [props.userObj])

    return (
        <React.Fragment>
            <div className={
                            "auth-container auth-container-wide " +
                            ((isFirstScreen) ? "d-flex" : "d-none")
                        }>
                <div className="auth-aside d-flex justify-content-center">
                    <div className='auth-aside-container'>
                        <div className='auth-aside-heading'>Participate</div>
                        <div className='auth-aside-sub-heading'>in research, learn about yourself, and earn rewards.</div>
                    </div>
                </div>
                <div className='auth-main d-flex justify-content-center'>
                    <div className='auth-main-spacer'>

                        <div className='logo-container'>
                            <div className='logo-box'></div>
                        </div>
                        <div className='main-text mt-1'>
                            Join ProofPilot
                        </div>
                        <div className='sub-text'>
                            Continue with one of the following
                        </div>
                        <div className='social-login-btn-container'>
                            <div className='row'>
                                <div className='col'>
                                    <div className='d-flex justify-content-center'>
                                        <button className='social-login-btn'>
                                            <div className='social-btn-wrapper'>
                                                <div className='social-btn-logo google'></div>
                                                <div className='social-btn-spacer'></div>
                                                <div className='social-btn-text'>Google</div>
                                            </div>
                                        </button>
                                    </div>
                                </div>
                                <div className='col'>
                                    <div className='d-flex justify-content-center'>
                                        <button className='social-login-btn'>
                                            <div className='social-btn-wrapper'>
                                                <div className='social-btn-logo google'></div>
                                                <div className='social-btn-spacer'></div>
                                                <div className='social-btn-text'>Microsoft</div>
                                            </div>
                                        </button>
                                    </div>
                                </div>
                                <div className='col'>
                                    <div className='d-flex justify-content-center'>
                                        <button className='social-login-btn'>
                                            <div className='social-btn-wrapper'>
                                                <div className='social-btn-logo google'></div>
                                                <div className='social-btn-spacer'></div>
                                                <div className='social-btn-text'>Apple</div>
                                            </div>
                                        </button>
                                    </div>
                                </div>
                                <div className='col'>
                                    <div className='d-flex justify-content-center'>
                                        <button className='social-login-btn'>
                                            <div className='social-btn-wrapper'>
                                                <div className='social-btn-logo google'></div>
                                                <div className='social-btn-spacer'></div>
                                                <div className='social-btn-text'>Single Sign-On</div>
                                            </div>
                                        </button>
                                    </div>

                                </div>
                            </div>
                        </div>
                        <div className='sub-text'>
                            It's faster, easier, and more secure.
                        </div>
                        <div className='circle-with-line-in-center d-flex justify-content-center'>
                            <div className='main-circle'>or</div>
                        </div>
                        <div className="form-fields">
                            <div className='mb-4'>
                                <label htmlFor='email' className='form-label'>E-mail or phone number</label>
                                <input type='email' className={
                            "form-control " +
                            ((emailBlurred && !emailValid) ? "field-error-border" : "")
                        } onChange={handleEmailChange} id='email' value={email} onBlur={emailBlurChange} />
                        {(emailBlurred && !emailValid) && <p className='field-error'>Login is not correct</p>}
                        {/* {(props.userObj && props.userObj.error) && <p className='field-error' dangerouslySetInnerHTML={{ __html: props.userObj.error }}></p>} */}
                        {/* {(props.userObj.error) && <p className='field-error'>{props.userObj.error}</p>} */}
                        {(apiError) && <p className='field-error' dangerouslySetInnerHTML={{ __html: apiError }}></p>}
                            </div>
                        </div>
                        <div className='mt-4 mb-2 text-center'>
                            <button type="button" className="btn btn-primary auth-action-btn" onClick={emailSubmitHandler}>Continue</button>
                        </div>
                        <div className='sub-text mb-4'>
                            Already have an account? <a href="#" onClick={goToLoginPage}>Log In here.</a>
                        </div>
                        <div className='small-text mb-2'>
                            ProofPilot takes your security and privacy seriously. We collect contact information to secure access to the sensitive information you share.
                        </div>
                    </div>

                </div>
            </div>
            <div className={
                            "auth-container auth-container-wide " +
                            ((!isFirstScreen) ? "d-flex" : "d-none")
                        }>
                <div className="auth-aside d-flex justify-content-center">
                    <div className='auth-aside-container'>
                        <div className='auth-aside-heading'>Participate</div>
                        <div className='auth-aside-sub-heading'>in research, learn about yourself, and earn rewards.</div>
                    </div>
                </div>
                <div className='auth-main d-flex justify-content-center'>
                    <div className='auth-main-spacer'>

                        <div className='sub-text'>
                            Let's finish creating your account for
                        </div>
                        <div className='sub-text dynamic'>
                            john+smith@somedomain.com
                        </div>
                        <div className="form-fields">
                            <div className='mb-4'>
                                <label htmlFor='password' className='form-label'>Please choose your password</label>
                                <input type='password' className={'form-control ' + ((passwordError) ? "field-error-border" : "")} id='password' ref={inputPassRef} onChange={handlePassWordChange} />
                                <span className='form-info-text'>Minimum 8 characters with 1 letter and 1 number.</span>
                                {passwordError && <p className='field-error'>{passwordError}</p>}
                            </div>

                            <div className='mb-4'>
                                <label htmlFor='repassword' className='form-label'>Please re-type your password</label>
                                <input type='password'className={'form-control ' + ((passwordError) ? "field-error-border" : "")} id='repassword' ref={inputRePassRef} onChange={handlePassWordChange} />
                                {passwordError && <p className='field-error'>{passwordError}</p>}
                            </div>

                            <div className='form-check'>
                                <input className='form-check-input' type='checkbox' value='' id='flexCheckDefault' />
                                <label className='form-check-label' htmlFor='flexCheckDefault'>
                                    Keep me logged in for 30 days
                                </label>
                            </div>
                        </div>
                        <div className='small-text'>
                            Use this feature in combination with your device security features to make it easier to log in and use ProofPilot. If you share your device with others or do not use device security features, do not use this feature.
                        </div>
                        <div className='mt-4 mb-2 text-center'>
                            <button type="button" className="btn btn-primary auth-action-btn" onClick={passwordSubmitHandler}>Create Account and Log in</button>
                        </div>
                        <div className='sub-text mb-4'>
                            Already have an account? <a href="#" onClick={goToLoginPage}>Log in here.</a>
                        </div>
                        <div className='small-text mb-2'>
                            By continuing, you agree to periodic communications and reminders from ProofPilot. Any data you share with ProofPilot is secured and encrypted and will only be provided to studies with your expressed permission. Before you join each study, we'll tell you all you need to know about the risks and rights you have. We use cookies to improve your user experience.
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default SignUp;